@import "https://fonts.googleapis.com/css?family=Roboto:500&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Roboto, Arial, sans-serif;
  line-height: 18px;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__img {
  width: 40px;
  height: 40px;
}

.header__img img {
  width: 100%;
}

.header__list {
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__item {
  justify-content: center;
  align-items: center;
  height: 60px;
  display: flex;
}

.header__item:not(:last-child) {
  margin-right: 20px;
}

.header__link {
  text-transform: uppercase;
  color: #060b35;
  padding: 22px 0;
  text-decoration: none;
  transition: color .3s;
}

.header__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  padding: 23px 0;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 2px;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.product-card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .2s;
}

.product-card:hover {
  transform: scale(1.2);
  box-shadow: 0 4px 8px #0000001a;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  transition: color .2s;
}

.product-card:hover .product-card__name {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__info {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product-card__review {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product-card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-title {
  font-size: 12px;
}

.product-card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.product-card__buy {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: inline-block;
}

.product-card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
